<template>
  <div class="main">
    <firstNav :firstNav="firstNav">
      <div class="left-item">
        <div class="left-user">
          <div class="user-infor">
            <span class="user-img">
              <img :src="userImg" alt />
            </span>
            <span class="user-name">
              {{ userName }}
            </span>
          </div>
          <el-badge
            @click.native="jumpCheckRouter"
            title="任务流程"
            :max="99"
            class="item left-process"
          >
            <el-button
              type="text"
              class="process"
              icon="el-icon-set-up"
            ></el-button>
          </el-badge>

          <el-badge
            :is-dot="isDot"
            title="消息"
            class="right-message"
          >
            <el-button
              type="text"
              class="message"
              icon="el-icon-bell"
            ></el-button>
          </el-badge>
          <!-- <el-popover
            placement="top-start"
            title="用户信息"
            width="200"
            trigger="hover"
            class="left__btn"
            :content="loginInfo"
          >
            <i slot="reference" class="el-icon-user icon-user"></i>
          </el-popover> -->
        </div>
      </div>
    </firstNav>
    <div class="container">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
import firstNav from "@/components/nav/firstNav";
import menuList from "@/assets/js/menuList";

export default {
  name: "index",
  components: { firstNav },
  data() {
    return {
      userImg: require("@/assets/img/userImg.jpg"),
      userInfo: this.$store.getters.userInfo,
      userName: "",
      firstNav: {
        title: "ITMS服务系统",
        logoSrc: require("../assets/img/logo.png"),
        navData: {
          navList: menuList.firstNav, //一级菜单结构,
          //默认路径
          activeChanel: ""
        }
      },
      isDot: true
    };
  },
  computed: {
    loginInfo() {
      let { userName, companyName, phone } = this.userInfo;
      return `${userName}(${phone})\n${companyName}`;
    }
  },

  mounted() {
    //userInfo
    if (this.userInfo) {
      this.userName = this.userInfo.name;
    } else {
      this.userName = "未登录";
    }
  },
  methods: {
    jumpCheckRouter() {
      let path = "/flowManage"
      //   ? "/flowManagement/pendingFlow"
      //   : "/flowManagement";
      this.$router.push({
        path: path
      });
    }
  }
};
</script>
<style lang="scss">
.main {
  width: 100vw;
  height: 100vh;
  font-size: 16px;
  color: $fontColor;
}

.container {
  padding: 20px;
  width: 100%;
  height: calc(100% - 68px);
  background: #f2f6fa;
}

.container > div {
  width: 100%;
  height: 100%;
}
.left-item {
  .left-user {
    font-size: 16px;
    display: flex;
    align-items: center;
  }
  .user-infor {
    display: flex;
    height: 40px;
    line-height: 40px;
  }
  .user-img {
    width: 40px;
    height: 40px;
    border-radius: 20px;
    overflow: hidden;
    img {
      height: 115%;
    }
  }
  .user-name {
    margin: 0px 10px;
  }
  .el-badge__content.is-fixed.is-dot {
    top: 15px;
    right: 10px;
  }
  .right-message {
    margin-right: 5px;
  }
  .left-process{
    margin: 0px 10px;
  }
  .message , .process {
    font-size: 25px;
    color: #fff;
  }
}

.left-item .icon-home,
.left-item .icon-user {
  padding: 0 4px;
  cursor: pointer;
  /* font-weight: bold; */
}
.process:hover,
.message:hover,
.left-item .icon-home:hover,
.left-item .icon-user:hover {
  font-weight: bold;
  color: #fff;
}
</style>
