<template>
  <div class="firstNav">
    <div class="nav-logo">
      <img :src="logoSrc" alt />
      <span class="separate"></span>
      <label>{{title}}</label>
    </div>
    <div id="nav-box">
      <ul class="nav-list">
        <template v-for="(item,index) in navData.navList">
          <li
            :key="index"
            @click="channelJump(item)"
            v-if="!item.isHide"
            :class="{active: isAction(item)}"
          >
            <div class="navlist-item-text">
              <i v-if="item.icon" :class="item.icon " class="navlist-item-icon"></i>
              <span class="item-text">{{item.Navtitle}}</span>
              <!-- <div class="underline" v-if="isAction(item)"></div> -->
            </div>
          </li>
        </template>
      </ul>
    </div>
    <div class="nav-setting">
      <slot></slot>
    </div>
  </div>
</template>
<script>
export default {
  name: "firstNav",
  props: {
    firstNav: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {
      title: "系统名称",
      logoSrc: "",
      navData: {
        navList: [],
        activeChanel: ""
      }
    };
  },
  created() {
    this.init();
  },
  watch: {
    firstNav: {
      handler(val) {
        if (val) {
          this.init();
        }
      },
      deep: true
    },
    "$route.path": {
      handler(val) {
        this.changeActive();
      },
      deep: true
    }
  },
  mounted() {
    this.$nextTick(() => {
      // this.navData.activeChanel = this.$route.matched[1].path;
      this.changeActive();
    });
  },
  methods: {
    init() {
      let { title, logoSrc, navData } = this.firstNav;
      this.navData = Object.assign({}, this.navData, navData);
      this.title = title || this.title;
      this.logoSrc = logoSrc || this.logoSrc;
    },
    //导航点击
    channelJump(data) {
      /*
       * 以http开头的需要跳转
       *
       *router 以name跳转的params传参
       *router 以path跳转的query传参 (vue-router 定义)
       * */
      this.$emit("changeNav", data);

      if (data.hasOwnProperty("url")) {
        if (data.url !== "") {
          window.open(data.url, "_self");
          this.$set(this.navData, "activeChanel", data.url);
        }
      } else if (data.hasOwnProperty("name")) {
        if (data.name !== "") {
          if (data.hasOwnProperty("params")) {
            this.$router.push({ name: data.name, params: data.params });
          } else {
            this.$router.push({ name: data.name });
          }
          this.$set(this.navData, "activeChanel", data.name);
        }
      } else if (data.hasOwnProperty("path")) {
        if (data.path !== "") {
          if (data.hasOwnProperty("query")) {
            this.$router.push({ path: data.path, query: data.query });
          } else {
            this.$router.push({ path: data.path });
          }
          this.$set(this.navData, "activeChanel", data.path);
        }
      }
    },
    //判断是否启用
    isAction(item) {
      return (
        item.path == this.navData.activeChanel ||
        item.name == this.navData.activeChanel ||
        item.url == this.navData.activeChanel
      );
    },
    //切换选中状态
    changeActive() {
      let strurl = this.$route.path;
      let isSecondToFirst = this.$store.getters["isSecondToFirst"];

      if (isSecondToFirst) {
        let { firstNav,secondNav } = this.$store.getters["allAuth"];
        for (const key in secondNav) {
          let arr = secondNav[key];
          let str = key;
          if (arr.length) {
            str = JSON.stringify(arr);
          }

          if (str.indexOf(strurl) != -1) {
            strurl = key;
            this.$set(this.navData, "activeChanel", strurl);
          }
        }

      } else {
        strurl = strurl.match(/^(\/[\w]+)\/?/)[1];
        this.$set(this.navData, "activeChanel", strurl);
      }
    }
  }
};
</script>
